const N3 = require('n3');
const fs = require('fs').promises;
const { DataFactory } = N3;
const { namedNode, literal } = DataFactory;

async function convertTtlToJson(inputFile, outputFile) {
    const parser = new N3.Parser();
    const store = new N3.Store();
    const shapes = {};

    const ttlData = await fs.readFile(inputFile, 'utf8');
    await new Promise((resolve, reject) => {
        parser.parse(ttlData, (error, quad, prefixes) => {
            if (error) return reject(error);
            if (quad) store.addQuad(quad);
            else resolve();
        });
    });

    //  NodeShape
    store.getQuads(null, namedNode('http://www.w3.org/1999/02/22-rdf-syntax-ns#type'), namedNode('http://www.w3.org/ns/shacl#NodeShape')).forEach(quad => {
        const shapeId = quad.subject.value;
        shapes[shapeId] = {
            id: shapeId,
            targetClass: null,
            properties: []
        };
    });

    //  targetClass
    store.getQuads(null, namedNode('http://www.w3.org/ns/shacl#targetClass'), null).forEach(quad => {
        if (shapes[quad.subject.value]) {
            shapes[quad.subject.value].targetClass = quad.object.value;
        }
    });

    //  
    store.getQuads(null, namedNode('http://www.w3.org/ns/shacl#property'), null).forEach(quad => {
        const shapeId = quad.subject.value;
        if (shapes[shapeId]) {
            shapes[shapeId].properties.push({ propertyId: quad.object.value });
        }
    });

    //   
    for (const shape of Object.values(shapes)) {
        for (const prop of shape.properties) {
            const propQuads = store.getQuads(namedNode(prop.propertyId), null, null);
            prop.path = store.getQuads(namedNode(prop.propertyId), namedNode('http://www.w3.org/ns/shacl#path'), null)[0]?.object.value;
            prop.minCount = store.getQuads(namedNode(prop.propertyId), namedNode('http://www.w3.org/ns/shacl#minCount'), null)[0]?.object.value;
            prop.nodeKind = store.getQuads(namedNode(prop.propertyId), namedNode('http://www.w3.org/ns/shacl#nodeKind'), null)[0]?.object.value;
            prop.datatype = store.getQuads(namedNode(prop.propertyId), namedNode('http://www.w3.org/ns/shacl#datatype'), null)[0]?.object.value;
            prop.class = store.getQuads(namedNode(prop.propertyId), namedNode('http://www.w3.org/ns/shacl#class'), null)[0]?.object.value;

            //  sh:or
            const orQuads = store.getQuads(namedNode(prop.propertyId), namedNode('http://www.w3.org/ns/shacl#or'), null);
            if (orQuads.length > 0) {
                prop.or = [];
                const orList = store.getQuads(orQuads[0].object, null, null);
                orList.forEach(orItem => {
                    const orNode = orItem.object;
                    const orDetails = store.getQuads(orNode, null, null);
                    const orProp = {};
                    orDetails.forEach(detail => {
                        if (detail.predicate.value === 'http://www.w3.org/ns/shacl#nodeKind') {
                            orProp.nodeKind = detail.object.value;
                        }
                        if (detail.predicate.value === 'http://www.w3.org/ns/shacl#datatype') {
                            orProp.datatype = detail.object.value;
                        }
                    });
                    prop.or.push(orProp);
                });
            }
        }
    }

    //   JSON
    const jsonOutput = { shapes: Object.values(shapes) };
    await fs.writeFile(outputFile, JSON.stringify(jsonOutput, null, 2));
    console.log('JSON saved to', outputFile);
}

convertTtlToJson('/mnt/e/LU/Bachelor/qseoutput/dblp_books_QSE_FULL_SHACL_corrected.ttl', '/mnt/e/LU/Bachelor/dblp_books_schema.json')
    .catch(err => console.error('Error:', err));